.header {
    height:10vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: aquamarine;
}

.header h1 {
    font-family: cursive;
}

.mainbody {
    background-color: rgb(223, 244, 237);
    margin-top: 30px;
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    gap: 0.5rem;
}

.flip-card {
    background-color: transparent;
    width: 300px;
    height: 300px;
    perspective: 1000px;
    margin: 10px;
    cursor: pointer;
}

.flip-card-inner {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
    transition: transform 0.6s;
    transform-style: preserve-3d;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

.flip-card:hover .flip-card-inner {
    transform: rotateY(180deg);
}

.flip-card-front,
.flip-card-back {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
}

.flip-card-front {
    background-color: #252323;
    color: black;
}


.flip-card-front img{
    width: 300px;
    height: 250px;
}

.flip-card-front p {
    color: white;
}
.flip-card-back {
    background-color: #2980b9;
    color: white;
    transform: rotateY(180deg);
}

.loader {
    border: 16px solid #f3f3f3;
    /* Light grey */
    border-top: 16px solid #3498db;
    /* Blue */
    border-radius: 50%;
    width: 120px;
    height: 120px;
    animation: spin 2s linear infinite;
    margin: auto;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

.pagination-container {
    display: flex;
    justify-content: center;
    margin-top: 20px;
}

.pagination-button {
    border: none;
    background-color: #fff;
    color: #555;
    padding: 10px 15px;
    margin: 0 5px;
    font-size: 16px;
    cursor: pointer;
    border-radius: 5px;
    transition: all 0.2s ease-in-out;
}

.pagination-button:hover,
.pagination-button.active {
    background-color: #555;
    color: #fff;
}

.flip-card:hover .flip-card-back div {
    display: block;
    animation: fade-in 1s ease-in-out;
}

.flip-card-author {
    font-size: 20px;
    margin-top: 20px;
}

.flip-card-link {
    margin-top: 20px;
}

.flip-card-excerpt {
    margin-top: 20px;
    font-size: 16px;
}

@keyframes fade-in {
    0% {
        opacity: 0;
        transform: translateX(-20px);
    }

    100% {
        opacity: 1;
        transform: translateX(0);
    }
}